.listing {
    position: relative;
    cursor: pointer;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
    margin-top: 7%;
  }
  
  .listing::before {
    content: "";
    display: block;
    padding-bottom: calc((5 / 4) * 100%);
  }
  
  .listing__content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
  }
  
  .listing__image-container {
    width: 100%;
    height: 65%;
    overflow: hidden;
  }
  
  .listing__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .listing__details {
    height: 35%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 0px 25px;
    position: relative;
  }
  
  .listing__type {
    position: absolute;
    font-size: 0.8rem;
    right: 10px;
    top: 10px;
    background-color: rgb(223, 247, 255);
    padding: 5px 10px;
    border-radius: 15px;
  }
  
  .listing__row {
    margin: 6px 0px;
    display: flex;
    align-items: center;
  }
  
  .listing__price {
    font-size: 1.5rem;
  }
  
  .listing__address {
    color: #737373;
  }